

.no-webp .imagen-contacto{
  background-image: url('../img/fondo_contacto.jpg');
}

.webp .imagen-contacto{
    background-image: url('../img/fondo_contacto.webp');

}
.imagen-contacto{
    background-position: center center;
    background-size : cover;
    padding: $separacion 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    h2{
        font-size: 4rem;
        font-weight: $black;
        color: $blanco;
    }
    p{
        font-size: 2rem;
        color: $blanco;
    }

}